body{
  background-color: rgb(54,217,182);
  background: linear-gradient(90deg,rgba(54,217,182,1), 0%,rgba(32,152,126,1));
}

h1,h2,h3,h4,h5,p,span,strike{
  font-family: 'Montserrat',sans-serif;
}

#task-container{
  max-width: 600px;
  margin: 0 auto;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  background: #fff;
  margin-top: 100px;
  margin-bottom: 100px;
  align-content: space-around;
  align-items: flex-start;
}

#form-wrapper{
  position: -webkit-sticky;
  position: sticky;
  top:0rem;
  border-bottom: 1px solid #e9e9e9;
  background-color: #fff;
  padding:48px;
  /*box-shadow: 0 3px 8px rgba(0,0,0,0.25));*/

}
#submit{
  background: #36d9b6;
  border-radius: 0;
  border: 0;
  color: #fff;
}

.flex-wrapper{
  display:flex
}

.task-wrapper{
  cursor: pointer;
  border-bottom: 1px solid #e9e9e9;
  color: #686868;
  margin: 5px;
  padding: 20px;
}

.task-wrapper:hover{
  background-color: #F0F0F0;
}